import Nav from "./Nav";
import Message from "./Message";
import { Box, FormErrorMessage, FormLabel, FormControl, Input, Button, Heading } from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

const UserMessages = () => {
    const [messages, setMessages] = useState([]);
    let email = useParams().email;

    useEffect(() =>{
        fetch('/api/chat')
            .then(res => res.json())
            .then(data => setMessages(data.filter(m => m.email == email)))
            .catch(err => console.error(err))
    }, [messages]);
    return(
        <Box>
            <Nav/>
            <Box maxWidth={"60%"} 
                m={"auto"}
                >
                <Heading>My Messages</Heading>
                {messages.length > 0 ? messages.map(m => <Message key={m._id} body={m.body} email={m.email} type={m.type}/>) : <Heading color={"orange"}>ERROR: Messages not found</Heading>}

            </Box>
        </Box>
    )
}

export default UserMessages;