import React, { useEffect, useState } from "react";
import { Box, FormErrorMessage, FormLabel, FormControl, Input, Button, Heading, useToast } from "@chakra-ui/react";
import { useForm } from 'react-hook-form';
import { useParams, useNavigate, NavLink } from "react-router-dom";

const Register = () => {
    let id = useParams().id;
    const navigate = useNavigate();
    const toast = useToast();
    const {
        handleSubmit,
        register,
        reset,
        formState: { errors },
    } = useForm()

    const onSubmit = async (data) => {
        const sendData = await fetch(`/auth/register`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        })

        try {
            const response = await sendData.json();
            console.log(response);
            fetch('/api/mail/signup', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            })
            toast({
                title: 'User Created Successfully',
                duration: 5000,
                isClosable: true,
            })
            if(response.login == 1) navigate('/');
        } catch (error) {
            toast({
                title: 'User Already Exists',
                duration: 5000,
                isClosable: true,
                status: 'error'
            })
        }
        reset();
    }
    return(
        <Box width={"100vw"} height={"100vh"} display={"flex"} justifyContent={"center"} alignContent={"center"}>
            <Box width={"30%"} margin={"auto"}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <Heading size={"lg"} mb={"1rem"}>Register</Heading>

                    {/* EMAIL */}
                    <FormControl isRequired isInvalid={errors.email} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='username'>Email</FormLabel>
                        <Input
                            id='username'
                            placeholder='example@email.com'
                            type={'email'}
                            {...register('username', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.username && errors.username.message}
                        </FormErrorMessage>
                    </FormControl>
                    
                    {/* PASSWORD */}
                    <FormControl isRequired isInvalid={errors.password} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='password'>Password</FormLabel>
                        <Input
                            id='password'
                            placeholder='********'
                            type={"password"}
                            {...register('password', {
                                required: 'This field is required',
                                minLength: { value: 8, message: 'Minimum length should be 8' },
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.password && errors.password.message}
                        </FormErrorMessage>
                    </FormControl>
                    {/* NAME */}
                    <FormControl isRequired isInvalid={errors.name} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='name'>Name</FormLabel>
                        <Input
                            id='name'
                            placeholder='John'
                            type={"text"}
                            {...register('name', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.name && errors.name.message}
                        </FormErrorMessage>
                    </FormControl>

                    {/* ADDRESS */}
                    <FormControl isRequired isInvalid={errors.address} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='address'>Address</FormLabel>
                        <Input
                            id='address'
                            placeholder='1st Av. 4323'
                            type={"text"}
                            {...register('address', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.address && errors.address.message}
                        </FormErrorMessage>
                    </FormControl>

                    {/* AGE */}
                    <FormControl isRequired isInvalid={errors.age} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='age'>Age</FormLabel>
                        <Input
                            id='age'
                            placeholder='23'
                            type={"number"}
                            {...register('age', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.age && errors.age.message}
                        </FormErrorMessage>
                    </FormControl>

                    {/* PHONE NUMBER */}
                    <FormControl isRequired isInvalid={errors.phone} padding={"0.7rem 0rem"}>
                        <FormLabel htmlFor='phone'>Phone Number</FormLabel>
                        <Input
                            id='phone'
                            placeholder='1122334455'
                            type={"number"}
                            {...register('phone', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.phone && errors.phone.message}
                        </FormErrorMessage>
                    </FormControl>

                    <Button colorScheme={"orange"} borderRadius={"none"} type="submit" mt={"1rem"}>
                        REGISTER
                    </Button>
                    <NavLink to={'/login'}>
                        <Button  colorScheme={"orange"} variant={"ghost"} borderRadius={"none"} ml={"0.5rem"} mt={"1rem"}>
                            GO TO LOGIN
                        </Button>
                    </NavLink>
                </form>
            </Box>
        </Box>
    );
}

export default Register;