import React, { Component } from 'react'
import { Box, Heading, Badge } from '@chakra-ui/react'


const Message = ({email, body, type}) => {
    return (
        <Box maxWidth={"50rem"}
            width={"fit-content"}
            bgColor={type !== 'user' ? 'green': '#363034'}
            textAlign={type === 'user' ? 'right': 'left'}
            mt={"1rem"}
            ml={type !== 'user' ? '0': 'auto'}
            mr={type !== 'user' ? 'auto': '0'}
            p={"0.6rem 1rem"}
            >
            <Badge size={"sm"}>
                {email}
            </Badge>
            <Heading size={"sm"} mt={"0.5rem"}>
                {body}
            </Heading>
        </Box>
    )
}

export default Message