import React from 'react'
import { Box, Heading, Button } from '@chakra-ui/react';
import { NavLink, useNavigate } from 'react-router-dom';

const Nav = () => {
    const navigate = useNavigate();
    const logout = async () => {
        try {
            const response = await fetch('/auth/logout');
            const data = await response.json();
            navigate('/');
        } catch (error) {
            console.error(error);
        }
    }

    return (
      <Box  display={"flex"} 
            justifyContent={"space-between"} 
            alignItems={"center"}
            maxWidth={"1350px"}
            m={"auto"}
            pt={"1rem"}>
        
        <NavLink to={'/'}>
            <Heading width={"fit-content"} size={"lg"} textAlign={"center"}>
                BOOK STORE
            </Heading>
        </NavLink>
        
        <Box    width={""} 
                display={"flex"} 
                justifyContent={"space-between"} 
                alignItems={"center"}>
            <NavLink to={"/"}>
                <Button borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    HOME
                </Button>
            </NavLink>

            <NavLink to={"/add"}>
                <Button borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    ADD PRODUCT
                </Button>
            </NavLink>

            <NavLink to={"/cart"}>
                <Button borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    CART
                </Button>
            </NavLink>

            <NavLink to={"/chat"}>
                <Button borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    CHAT
                </Button>
            </NavLink>

            <NavLink to={"/profile"}>
                <Button borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    PROFILE
                </Button>
            </NavLink>

            <Button onClick={logout} borderRadius={"none"} size={"sm"} m={"0rem 0.5rem"} colorScheme={"orange"}>
                    LOGOUT
            </Button>
        </Box>
      </Box>
    )
}

export default Nav;