import React, { useEffect, useState } from "react";
import { Box, Spinner, Heading, Button, useToast } from "@chakra-ui/react";
import CartItem from "./CartItem";
import Nav from "./Nav";

const Cart = () => {
    const [items, setItems] = useState([]);
    const [user, setUser] = useState('');
    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(false);
    const toast = useToast();

    useEffect( () => {
        fetch('/auth/logged')
            .then(loginInfo => loginInfo.json())
            .then(login => {
                if(login.login === 1) {
                    fetch(`/api/cart/${login.username}/products`)
                        .then(response => response.json())
                        .then(data => {
                            setItems(data);
                            setUser(login.username);
                            setTotal(items.reduce((a, b) => a + (b.price * b.quant), 0));
                        })
                        .catch(error => console.error(error))
                }
            })
    }, [items]);

    const checkout = async () => {
        setLoading(true);
        let order = {
            products: items,
            username: user,
            price: total,
            timestamp: new Date().toLocaleString()
        }
        fetch('/api/order', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(order)
        })
            .then(res => res.json())
            .then(ready => {
                if(ready !== undefined){
                    fetch('/api/mail/order', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(order)
                    })
                    fetch(`/api/cart/${user}/products`, {
                        method: 'DELETE'
                    })
                        .then(() => {
                            setItems([]);
                            setTotal(0);
                            setLoading(false);
                        })
                        .catch(err => console.error(err))

                    toast({
                        title: `Thanks for your purchase!`,
                        description: `You will receive an email with forward detail.`,
                        status: 'success',
                        duration: 5000,
                        isClosable: true,
                        })
                }
            })
            .catch(err => console.error(err))
    }

    return(
        <Box>
            <Nav/>
            {
                user !== '' ?
                <Box width={"50%"} 
                    m={"2rem auto 1rem auto"} 
                    display={"flex"} 
                    justifyContent={"space-between"} 
                    alignContent={"center"}
                    bg={"#363034"}
                    padding={"1rem"}
                    >
                    <Heading color={"#50B2C0"}>Total: ${total}</Heading>
                    <Button bgColor={"#50B2C0"} 
                            color={"white"} 
                            borderRadius={"0"}
                            onClick={checkout}
                            isDisabled={total <= 0 || loading}
                            >CHECKOUT</Button>

                </Box>
                :
                null
            }
            <Box display={"flex"} 
                width={"100%"} 
                justifyContent={"start"} 
                alignItems={"center"}
                gap={"1rem"}
                flexWrap={"wrap"}
                maxWidth={"1350px"}
                m={"auto"}
                pt={"1rem"}>
                
                { 
                    user !== '' ?
                    items.map(b => 
                    <CartItem   key={items.findIndex(i => i === b)} 
                                id={b.id} 
                                title={b.title} 
                                price={b.price}
                                img={b.img}
                                timestamp={b.timestamp}
                                username={user}
                                quantity={b.quant}>
                    </CartItem>)
                    :
                    <Box display={"grid"} placeItems={"center"} size={"xl"} width={"100%"}>
                        <Spinner></Spinner>
                    </Box>
                }
            </Box>
        </Box>
    )
}

export default Cart;