import React, { useEffect, useState } from "react";
import { Box, FormErrorMessage, FormLabel, FormControl, Input, Button, Heading, useToast } from "@chakra-ui/react";
import { useForm } from 'react-hook-form';
import { useParams, useNavigate, NavLink } from "react-router-dom";


const Login = () => {
    let id = useParams().id;
    const navigate = useNavigate();
    const toast = useToast();
    const {
        handleSubmit,
        register,
        reset,
        formState: { errors },
    } = useForm()

    const onSubmit = async (data) => {
        
        const sendData = await fetch(`/auth/login`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        })

        try {
            const response = await sendData.json();
            toast({
                title: 'Welcome!',
                duration: 5000,
                isClosable: true
            })
            if(response.login == 1) navigate('/');
        } catch (error) {
            toast({
                title: 'Invalid Username or Password',
                duration: 5000,
                isClosable: true,
                status: 'error'
            })
        }
        
        reset();
    }
    return(
        <Box width={"100vw"} height={"100vh"} display={"flex"} justifyContent={"center"} alignContent={"center"}>
            <Box width={"30%"} margin={"auto"}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <Heading size={"lg"} mb={"1rem"}>Login</Heading>
                    <FormControl isRequired isInvalid={errors.username} padding={"1rem 0rem"}>
                        <FormLabel htmlFor='username'>Username</FormLabel>
                        <Input
                            id='username'
                            placeholder='Username'
                            {...register('username', {
                                required: 'This field is required'
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.username && errors.username.message}
                        </FormErrorMessage>
                    </FormControl>

                    <FormControl isRequired isInvalid={errors.password} padding={"1rem 0rem"}>
                        <FormLabel htmlFor='password'>Password</FormLabel>
                        <Input
                            id='password'
                            placeholder='Password'
                            type={"password"}
                            {...register('password', {
                                required: 'This field is required',
                                minLength: { value: 8, message: 'Minimum length should be 8' },
                            })}
                            focusBorderColor={"#50B2C0"}
                            border={"1px solid #FEEFDD"}
                            bg={"#201E1F"}
                            color={"#50B2C0"}
                            borderRadius={"0px"}
                        />
                        <FormErrorMessage>
                            {errors.password && errors.password.message}
                        </FormErrorMessage>
                    </FormControl>

                    <Button colorScheme={"orange"} borderRadius={"none"} type="submit" mt={"1rem"}>
                        LOGIN
                    </Button>

                    <NavLink to={'/register'}>
                        <Button  colorScheme={"orange"} variant={"ghost"} borderRadius={"none"} ml={"0.5rem"} mt={"1rem"}>
                            GO TO REGISTER
                        </Button>
                    </NavLink>
                </form>
            </Box>
        </Box>
    );
}

export default Login;