import Nav from "./Nav";
import { Box, FormErrorMessage, FormLabel, FormControl, Input, Button, Heading } from "@chakra-ui/react";
import io from 'socket.io-client';
import { useEffect, useState } from "react";
import Message from "./Message";
import { useForm } from 'react-hook-form';
const socket = io.connect('http://localhost:8080');


const Chat = () => {
    const [user, setUser] = useState({});
    const [messages, setMessages] = useState([]);
    socket.emit('getMessages');
    const {
        handleSubmit,
        register,
        reset,
        formState: { errors },
    } = useForm()
    
    useEffect( () => {
        fetch('/auth/logged')
            .then(loginInfo => loginInfo.json())
            .then(login => {
                if(login.login === 1) {
                    setUser(login.user[0]);
                }
            })
    }, [user]);

    useEffect(() => {
        socket.on('messages', (messages) => {
            setMessages(messages);
        })
    }, [socket]);

    const onSubmit = async (data) => {
        if(user != {}){
            const message = {
                body: data.message,
                email: user.email,
                type: user.email == "admin@admin.com" ? "system" : "user",
                timestamp: new Date().toLocaleString()
            }
    
            await socket.emit('message', message);
        }
        reset();
    }

    return(
        <Box pb={"3rem"}>
            <Nav/>
            <Box maxWidth={"60%"} 
                m={"auto"}
                >
                <form onSubmit={handleSubmit(onSubmit)}>
                    <Box display={"flex"} 
                        justifyContent={"space-between"} 
                        bg={"#363034"} 
                        alignContent={"center"} 
                        maxWidth={"80%"} 
                        m={"1.5rem auto"}>
                        <FormControl isRequired 
                            isInvalid={errors.body} 
                            padding={"0.7rem 0rem"} 
                            display={"flex"}
                            justifyContent={"center"}
                            alignContent={"center"}>
                            <FormLabel htmlFor='message' m={"auto 1rem"}>Message</FormLabel>
                            <Input
                                id='message'
                                placeholder='Hi! How are you'
                                type={'text'}
                                {...register('message', {
                                    required: 'This field is required'
                                })}
                                focusBorderColor={"#50B2C0"}
                                border={"1px solid #FEEFDD"}
                                bg={"#201E1F"}
                                color={"#50B2C0"}
                                borderRadius={"0px"}
                                maxWidth={"80%"}
                            />
                            <FormErrorMessage>
                                {errors.message && errors.username.message}
                            </FormErrorMessage>
                        </FormControl>
                        <Button bgColor={"#50B2C0"} colorScheme={"orange"} borderRadius={"none"} type="submit" m={"0.7rem 2rem"}>
                            SEND
                        </Button>
                    </Box>
                </form>
                {messages.map(m => <Message key={m._id} body={m.body} email={m.email} type={m.type}/>)}

            </Box>
        </Box>
    )
}

export default Chat;