import React from "react";
import { Box, Heading, Button, Image, Badge, useToast } from "@chakra-ui/react";

const CartItem = ({ id, title, price, img, timestamp, username, quantity }) => {
    const toast = useToast();

    const removeFromCart = async () => {
        let removeProd = await fetch( `/api/cart/${username}/products/${id}`, {
            method: 'DELETE'
        })

        let response = await removeProd.json();
        toast({
            title: `${response} removed from cart`,
            description: `You can checkout or keep browsing`,
            duration: 3000,
            isClosable: true,
        })

    }

    return (
        <Box display={"flex"}
            flexDirection={"column"}
            justifyContent={"center"}
            alignItems={"start"}
            width={"200px"}
            padding={"1rem"}
            bg={"#363034"}>
            <Image m={"auto"} width={"150px"} height={"225px"} src={img}></Image>
            <Heading mt={"0.5rem"} size={"md"}>{title}</Heading>
            <Heading size={"md"} mb={"0.5rem"}>${price}</Heading>
            <Badge colorScheme={"orange"} borderRadius={"none"}>{timestamp}</Badge>
            <Badge colorScheme={"blue"} borderRadius={"none"} mt={"0.5rem"}>Quantity: {quantity}</Badge>
            <Button onClick={removeFromCart} borderRadius={"none"} colorScheme={"orange"} width={"100%"} mt={"0.8rem"}>Remove from cart</Button>
        </Box>
    )
}

export default CartItem;