import React from "react";
import Nav from "./Nav";
import { Box, Heading, Button } from "@chakra-ui/react"; 
import { useState, useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";

const UserProfile = () => {
    const [ user, setUser ] = useState('');
    const navigate = useNavigate();

    useEffect( () => {
        const fetchData = async () => {
            try {
                const response = await fetch('/auth/logged');
                const data = await response.json();
                setUser(data.user[0]);
            } catch (error) {
                console.error(error);
            }
        }
        fetchData();
    }, [user]);
    

    
    return(
        <Box>
            <Nav/>
            <Box width={"100%"} height={"90vh"} display={"flex"} justifyContent={"center"} alignContent={"center"}>
                <Box margin={"auto"}>
                    <Heading color={"#50B2C0"} size={"lg"}>
                        EMAIL
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.email}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        NAME
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.name}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        ADDRESS
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.address}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        AGE
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.age}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        PHONE
                    </Heading>
                    <Heading size={"md"}>
                        {user.phone}
                    </Heading>
                    <NavLink to={`/chat/${user.email}`}>
                        <Button borderRadius={"none"} size={"sm"} m={"1rem 0.5rem"} colorScheme={"orange"} bgColor={"#50B2C0"}>
                            MY MESSAGES
                        </Button>
                    </NavLink>
                </Box>
            </Box>
        </Box>
    );
}


export default UserProfile;