import { useEffect, useState } from 'react';
import { useParams, useNavigate } from "react-router-dom";
import Nav from "./Nav";
import { Box, Spinner, Heading, Image, Button, Badge, useToast } from "@chakra-ui/react";

const ProductDetail = () => {
    let id = useParams().id;
    const [item, setItem] = useState({});
    const [user, setUser] = useState('');
    let [quant, setQuant] = useState(0);
    const toast = useToast();
    const navigator = useNavigate();

    useEffect(() => {
        fetch('/auth/logged')
            .then(response => response.json())
            .then(login => {
                if (login.login === 1) {
                    fetch(`/api/products/${id}`)
                        .then(res => res.json())
                        .then(product => {
                            setItem(product);
                            setUser(login.username);
                        })
                        .catch(error => console.error(error))
                } else {
                    navigator('/login');
                }
            })
    }, [item, user]);

    const addToCart = async () => {
        let product = {
            ...item,
            timestamp: new Date().toLocaleString(),
            quant: quant
        };

        let addProd = await fetch(`/api/cart/${user}/products`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(product)
        })

        let response = await addProd.json();
        toast({
            title: `${response} added to cart`,
            description: `You can go to checkout or keep looking`,
            duration: 3000,
            isClosable: true,
        })
    }

    return (
        <Box>
            <Nav></Nav>
            {
                item.hasOwnProperty('error') ?
                <Box display={"flex"} justifyContent={"center"} alignItems={"center"} width={"100%"} height={"60vh"} m={"auto"}>
                    <Heading color={"orange.500"}>Error: Book not found</Heading>
                </Box>
                    :
                    item.hasOwnProperty('title') ?
                        <Box display={"flex"} justifyContent={"center"} alignItems={"center"} width={"100%"} height={"60vh"} m={"auto"}>
                            <Image src={item.img} bg={"#363034"} padding={"1rem"} width={"280px"} height={"420px"}></Image>
                            <Box display={"flex"} justifyContent={"center"} alignItems={"center"} flexDirection={"column"} m={"1rem 4rem"} width={"30%"}>
                                <Heading color={"orange.500"}>{item.title}</Heading>
                                <Heading color={"#50B2C0"} mt={"1rem"} mb={"2rem"}>${item.price}</Heading>
                                <Badge colorScheme={"blue"} fontSize={"1rem"}>Quantity</Badge>
                                <Box display={"flex"} >
                                    <Button color={"#50B2C0"}
                                        variant={"link"}
                                        fontSize={"1.5rem"}
                                        m={"0rem 1rem"}
                                        onClick={() => quant > 0 ? setQuant(quant - 1) : setQuant(0)}
                                    >-</Button>
                                    <Heading>{quant}</Heading>
                                    <Button color={"#50B2C0"}
                                        variant={"link"}
                                        fontSize={"1.5rem"}
                                        m={"0rem 1rem"}
                                        padding={"0.5rem"}
                                        onClick={() => setQuant(quant + 1)}
                                    >+</Button>
                                </Box>
                                <Button borderRadius={"none"}
                                    colorScheme={"orange"}
                                    width={"100%"}
                                    maxWidth={"20vw"}
                                    m={"1rem 0.2rem"}
                                    onClick={addToCart}
                                    isDisabled={!(quant > 0)}
                                >Add to cart</Button>
                            </Box>
                        </Box>
                        :
                        <Box display={"grid"} placeItems={"center"} size={"xl"}>
                            <Spinner></Spinner>
                        </Box>
            }
        </Box>
    )
}

export default ProductDetail;
